ber-color;\n    }\n    .blocks-literal-string {\n      white-space: pre;\n      color: @literal-string-color;\n    }\n    .blocks-literal-char {\n      color: @literal-char-color;\n    }\n    .blocks-literal-boolean {\n      color: @literal-boolean-color;\n    }\n    .blocks-literal-symbol {\n      color: @literal-symbol-color;\n    }\n    .blocks-lambdaExpression {\n      border-radius: @large-border-radius @large-border-radius 0px 0px;\n    }\n  }\n}\n","// Default parameters. You can override these when importing this file\n// just by declaring the same variable names _after_ the import.\n@border-radius: 5px;\n@border-width: 2px;\n\n@comment-color: #ddd;\n@comment-background-color: #444;\n@comment-border-color: (@comment-background-color - #111);\n@border-color: black;\n@editing-border-color: orange;\n@error-border-color: red;\n@hover-border-color: rgba(255, 165, 0, 0.3);\n@drag-drop-background-color: lightblue;\n@drag-replace-background-color: red;\n\n.react-container {\n  position: relative;\n}\n.react-container * {\n  vertical-align: top;\n}\n\n.searchBox {\n  background: black;\n  color: white;\n  font-weight: bold;\n  border: ridge;\n  position: absolute;\n  bottom: 0px;\n  right: 0px;\n  z-index: 10;\n  display: none;\n  min-width: 10px;\n  white-space: pre;\n  box-shadow: -2px -2px 3px #888;\n  &:before {\n    content: \"Find: \";\n    position: relative;\n    color: @editing-border-color;\n    right: 0px;\n  }\n}\n\n.blocks-structDefinition,\n.blocks-functionDefinition,\n.blocks-binop,\n.blocks-lambdaExpression,\n.blocks-variableDefinition,\n.blocks-functionApp,\n.blocks-ifExpression,\n.blocks-condExpression,\n.blocks-sequence,\n.blocks-whenUnlessExpr,\n.blocks-letLikeExpr,\n.blocks-unknown {\n  display: inline-flex;\n  flex-direction: column;\n  align-items: stretch;\n  color: black;\n  border: @border-width solid black;\n  border-radius: 15px;\n  margin: 2px;\n  background: #eee;\n  overflow: hidden;\n  white-space: nowrap;\n  transition: all 0.5s;\n\n 