n  margin-left: 15px;\n  border: 1px solid @comment-border-color;\n  text-shadow: 0 0 1px #000;\n  margin-bottom: -5px;\n  bottom: 0px;\n  left: 100%;\n  white-space: pre-line;\n  word-break: normal;\n  z-index: 999;\n}\n\n.blocks-comment::before {\n  content: \"\";\n  width: 0px;\n  height: 0px;\n  border: 0.5em solid transparent;\n  position: absolute;\n  left: -15px;\n  bottom: 15%;\n  border-right: 10px solid @comment-background-color;\n}\n.blocks-node:focus > .blocks-comment {\n  visibility: visible;\n}\n\n.blocks-dragging {\n  .blocks-white-space {\n    background-color: transparent;\n  }\n}\n\n.blocks-blank {\n  color: white;\n  background: gold;\n  cursor: hand;\n  &:after {\n    content: \"?\";\n    color: black;\n    display: inline-block;\n    width: 20px;\n  }\n  &.blocks-editing {\n    &:after {\n      content: none;\n      width: 0px;\n    }\n  }\n}\n.blocks-node[aria-expanded=\"false\"] {\n  max-height: 30px;\n  min-width: 75px;\n  max-width: 150px;\n  padding-left: 1px;\n  background: linear-gradient(to right, rgba(0, 0, 0, 1), white 90%);\n  * {\n    display: none;\n  }\n  .blocks-operator,\n  .blocks-operator * {\n    display: inline;\n    padding: 0px;\n    margin: 0px;\n    border: none;\n    font-weight: bold;\n    line-height: 20px;\n    color: white;\n    background: none;\n    text-align: left;\n  }\n  &.blocks-editing {\n    background: white;\n    max-width: none;\n    border-radius: 0px;\n  }\n}\n.blocks-node[aria-expanded=\"true\"]\n  .blocks-identifierList[aria-expanded=\"false\"]:not(.blocks-editing):before {\n  content: \"...\";\n  color: white;\n}\n.blocks-locked {\n  opacity: 0.5;\n}\n.blocks-locked::after {\n  content: \"\\1f512\" !important;\n  z-index: 99;\n}\n"],"sourceRoot":""}]);
